<template>
  <v-radio-group
    hide-details="auto"
    v-model="selectedOption"
  >
    <v-slide-group
      :key="String(reset)"
      v-intersect.once="onObserve"
      v-bind="$attrs"
      v-on="$listeners"
      id="custom-slide-group"
      :show-arrows="$vuetify.breakpoint.smAndUp && this.cards.length > 2 && 'always'"
    >
      <v-slide-item
        v-for="(card, i) in cards"
        :key="i"
        v-slot="{ active }"
      >
        <product-card
          :active="active"
          :height="height"
          :src="card.image_url"
          :width="width"
          v-bind="card"
          @click="onClick(card)"
          :selected="selectedOption === card.sku"
          :enable-text="enableText"
          :title="card.color"
          :ref="`card-${card.sku}`"
          :id="`card-${card.sku}`"
          :index="i"
        />
      </v-slide-item>
    </v-slide-group>
  </v-radio-group>
</template>

<script>
export default {
  name: 'ProductSlideView',
  components: {
    ProductCard: () => import('@/components/organism/ProductCard.vue')
  },
  props: {
    filter: {
      type: Function,
      default: (v) => !!v
    },
    height: {
      type: [Number, String],
      default: 125
    },
    width: {
      type: [Number, String],
      default: 175
    },
    cards: {
      type: Array,
      required: true
    },
    subtitleShow: {
      type: Boolean,
      default: true
    },
    img: {
      type: String,
      default: ''
    },
    enableText: {
      type: Boolean,
      default: true
    },
    onCardSelected: {
      type: Function,
      required: false,
      default: () => {}
    },
    defaultValue: {
      type: Object,
      required: false,
      default: () => {}
    }
  },

  data: () => ({
    reset: false,
    selectedOption: ''
  }),
  methods: {
    onObserve (x, y, isVisible) {
      if (this.reset) {
        if (this.defaultValue) {
          this.selectedOption = this.defaultValue.sku
          const { index } = this.$refs[`card-${this.defaultValue.sku}`][0].$el.attributes
          const elem = document.querySelector('#custom-slide-group > div.v-slide-group__next')
          for (let i = 0; i < Math.floor(index.value / 2); i++) {
            elem.click()
          }
        }
        return
      }
      setTimeout(() => {
        this.reset = !this.reset
      }, 100)
    },
    onClick (card) {
      this.selectedOption = card.sku
      this.onCardSelected(card)
    }
  },
  mounted () {}
}
</script>

<style>
.v-slide-group__content {
  justify-content: start;
}
</style>
